import Vue from 'vue'
import Router from 'vue-router'
const home = r => require.ensure([], () => r(require('../page/home/home')), 'home')
const main = r => require.ensure([], () => r(require('../page/main/main')), 'main')
const custom = r => require.ensure([], () => r(require('../page/custom/custom')), 'custom')
const index = r => require.ensure([], () => r(require('../page/entry/index')), 'index')
const shopCart = r => require.ensure([], () => r(require('../page/shopcart/shopCart')), 'shopCart')
const search = r => require.ensure([], () => r(require('../page/search/search')), 'search')
const notice = r => require.ensure([], () => r(require('../page/notice/noticeList')), 'notice')
const noticeContent = r => require.ensure([], () => r(require('../page/notice/noticeContent')), 'noticeContent')
const userSet = r => require.ensure([], () => r(require('../page/userset/userset')), 'userSet')
const bankcard = r => require.ensure([], () => r(require('../page/bankcard/bankcard')), 'bankcard')
const username = r => require.ensure([], () => r(require('../page/userset/username')), 'username')
const password = r => require.ensure([], () => r(require('../page/userset/password')), 'password')
const resetpsw = r => require.ensure([], () => r(require('../page/userset/resetpsw')), 'resetpsw')
const login = r => require.ensure([], () => r(require('../page/login/login')), 'login')
const register = r => require.ensure([], () => r(require('../page/register/register')), 'register')
const protocol = r => require.ensure([], () => r(require('../page/protocol/protocol')), 'protocol')
const seat = r => require.ensure([], () => r(require('../page/seat/seat')), 'seat')
const classify = r => require.ensure([], () => r(require('../page/classify/classify')), 'classify')
const store = r => require.ensure([], () => r(require('../page/store/store')), 'store')
const good = r => require.ensure([], () => r(require('../page/good/good')), 'good')
const submitOrder = r => require.ensure([], () => r(require('../page/submitOrder/submitOrder')), 'submitOrder')
const pay = r => require.ensure([], () => r(require('../page/pay/pay')), 'pay')
const myEarnings = r => require.ensure([], () => r(require('../page/myEarnings/myEarnings')), 'myEarnings')
const myCollection = r => require.ensure([], () => r(require('../page/myCollection/myCollection')), 'myCollection')
const myOrder = r => require.ensure([], () => r(require('../page/myOrder/myorder')), 'myOrder')
const orderDetail = r => require.ensure([], () => r(require('../page/orderDetail/orderDetail'), 'orderDetail'))
const refund = r => require.ensure([], () => r(require('../page/refund/refund')), 'refund')
const refundDetails = r => require.ensure([], () => r(require('../page/refund/refundDetails')), 'refundDetails')
const myRecommend = r => require.ensure([], () => r(require('../page/myRecommend/myRecommend')), 'myRecommend')
const recommendCode = r => require.ensure([], () => r(require('../page/myRecommend/recommendCode')), 'recommendCode')
const preferentialpay = r => require.ensure([], () => r(require('../page/store/preferentialpay')), 'preferentialpay')
const paySuccess = r => require.ensure([], () => r(require('../page/pay/paySuccess')), 'paySuccess')
Vue.use(Router)
Router.prototype.goback = function (){
  this.isback = true
  window.history.go(-1)
}
export default new Router({
  routes: [
    {
      path: '/',
      component: index,
      children: [
        {
          path: '',
          redirect: 'index'
        },
        {
          path: '/index',
          name: 'index',
          redirect: 'home',
          component: main,
          children: [
            {
              path: '/home',
              name: 'home',
              component: home
            },
            {
              path: '/custom',
              name: 'custom',
              component: custom,
            },
            {
              path: '/shopCart',
              name: 'shopCart',
              component: shopCart
            }
          ]
        },
        {
          path: '/search',
          name: 'search',
          component: search
        },
        {
          path: '/notice',
          name: 'notice',
          component: notice
        },
        {
          path: '/noticeContent',
          name: 'noticeContent',
          component: noticeContent
        },
        {
          path: '/userSet',
          name: 'userSet',
          component: userSet
        },
        {
          path: '/bankcard',
          name: 'bankcard',
          component: bankcard
        },
        {
          path: '/userSet/username',
          name: 'username',
          component: username
        },
        {
          path: 'userSet/password',
          name: 'password',
          component: password
        },
        {
          path: 'userSet/resetpsw',
          name: 'resetpsw',
          component: resetpsw
        },
        {
          path: '/login',
          name: 'login',
          component: login
        },
        {
          path: '/register',
          name: 'register',
          component: register
        },
        {
          path: '/register/protocol',
          name: 'protocol',
          component: protocol
        },
        {
          path: '/forgetPsw',
          name: 'forgetPsw',
          component: password
        },
        {
          path: '/resetpsw',
          name: 'resetpswNo',
          component: resetpsw
        },
        {
          path: '/seat',
          name: 'seat',
          component: seat
        },
        {
          path: '/classify',
          name: 'classify',
          component: classify
        },
        {
          path: '/store',
          name: 'store',
          component: store
        },
        {
          path: '/good',
          name: 'good',
          component: good
        },
        {
          path: '/submit',
          name: 'submit',
          component: submitOrder
        },
        {
          path: '/pay',
          name: 'pay',
          component: pay
        },
        {
          path: '/myEarnings',
          name: 'myEarnings',
          component: myEarnings
        },
        {
          path: '/myCollection',
          name: 'myCollection',
          component: myCollection
        },
        {
          path: '/myOrder',
          name: 'myOrder',
          component: myOrder
        },
        {
          path: '/orderDetail',
          name: 'orderDetail',
          component: orderDetail
        },
        {
          path: '/refund',
          name: 'refund',
          component: refund
        },
        {
          path: '/refundDetails',
          name: 'refundDetails',
          component: refundDetails
        },
        {
          path: '/myRecommend',
          name: 'myRecommend',
          component: myRecommend
        },
        {
          path: '/recommendCode',
          name: 'recommendCode',
          component: recommendCode
        },
        {
          path: '/preferentialpay',
          name: 'preferentialpay',
          component: preferentialpay
        },
        {
          path: '/paySuccess',
          name: 'paySuccess',
          component: paySuccess
        }
      ]
    }
  ]
})



